import {
  AwsKafkaSaslConfig,
  BaseConnectorConfig,
  isTopicRegex,
} from '@xip-online-data/types';
import { v4 as uuidv4 } from 'uuid';
import { KafkaJS } from '@confluentinc/kafka-javascript';
import { interval } from 'rxjs';
import {
  awsIamAuthenticator,
  Type,
} from '@jm18457/kafkajs-msk-iam-authentication-mechanism';
import { captureException } from '@sentry/node';
import {
  ConsumerConstructorConfig,
  KafkaConfig,
} from '@confluentinc/kafka-javascript/types/kafkajs';
import { Logger } from '@transai/logger';
import { MessageMonitorInterface } from './message-monitor.interface';
import { InMemoryMessageMonitor } from './in-memory.message-monitor';
import { RedisMessageMonitor } from './redis.message-monitor';

export abstract class AbstractRdKafkaService {
  protected readonly errorTypes = ['unhandledRejection', 'uncaughtException'];

  protected readonly signalTraps = ['SIGTERM', 'SIGINT', 'SIGUSR2'];

  public static readonly DEFAULT_CALLBACK_EVENT_TYPE = '*';

  protected readonly kafkaBase: KafkaJS.Kafka;

  protected readonly consumer: KafkaJS.Consumer;

  protected readonly producer: KafkaJS.Producer;

  protected readonly processedTopic: string = '_LOGS_xod';

  protected readonly DEFAULT_INTERVAL_CHECK_FOR_NEW_TOPICS = 300;

  protected numberOfSubscribedRegexTopics = 0;

  protected readonly disableLogs: boolean;

  protected messageMonitor: MessageMonitorInterface | undefined = undefined;

  public abstract init(): Promise<void>;

  protected readonly logger: Logger;

  public constructor(
    protected readonly baseYamlConfig: BaseConnectorConfig,
    protected readonly connectorTopic?: string | undefined,
  ) {
    this.logger = Logger.getInstance();
    const kafkaJS: KafkaConfig = {
      brokers: baseYamlConfig.kafka.brokers,
      ssl: false,
      clientId: `${baseYamlConfig.kafka.clientId}-${uuidv4()}`,
      logLevel: 2,
    };

    const authConfig = this.getAuthProviderOptions(baseYamlConfig.kafka.sasl);

    if (authConfig.sasl) {
      // @ts-ignore
      kafkaJS.sasl = authConfig.sasl;
      kafkaJS.ssl = true;
    }

    this.messageMonitor = this.getCorrespondingMonitor();

    this.disableLogs = baseYamlConfig.kafka.disableLogs ?? false;

    this.kafkaBase = new KafkaJS.Kafka({ kafkaJS });

    const consumerConfig: ConsumerConstructorConfig = {
      kafkaJS: {
        groupId: baseYamlConfig.kafka.groupId,
        autoCommitInterval: baseYamlConfig.kafka.autoCommitInterval ?? 5000,
        fromBeginning: true,
      },
    };

    if (baseYamlConfig.kafka.newConsumerProtocol) {
      this.logger.debug('Using new consumer protocol');
      consumerConfig['group.protocol'] = 'consumer';
      consumerConfig['group.protocol.type'] = 'consumer';
      consumerConfig['group.remote.assignor'] = 'uniform';
    }

    this.consumer = this.kafkaBase.consumer(consumerConfig);
    this.producer = this.kafkaBase.producer();

    if (
      !connectorTopic &&
      (this.baseYamlConfig.kafka.consumerTopics === undefined ||
        this.baseYamlConfig.kafka.consumerTopics.length === 0)
    ) {
      this.logger.warn(
        'No consumer topics set. skip setting of consumer events callback',
      );
      return;
    }

    this.getRegexTopics().then((topics) => {
      this.numberOfSubscribedRegexTopics = topics.length;
    });

    const intervalCheckForNewTopics =
      this.baseYamlConfig.kafka.intervalCheckForNewTopics ??
      this.DEFAULT_INTERVAL_CHECK_FOR_NEW_TOPICS;
    if (intervalCheckForNewTopics > 0) {
      interval(intervalCheckForNewTopics * 1000).subscribe(() => {
        this.checkForNewTopics().then(this.restartIfNewTopics.bind(this));
      });
    }

    if (process) {
      this.logger.debug('Setting up gracefull shutdown for kafka...');

      this.errorTypes.map((type) => {
        process.on(type, async (e) => {
          try {
            this.logger.error(`process.on ${type}`);
            this.logger.error(e);
            await this.exitProcess(type);
          } catch (_) {
            process.exit(1);
          }
        });
      });

      this.signalTraps.map((type) => {
        process.on(type, async () => {
          this.logger.error(`process.once ${type}`);
          try {
            await this.exitProcess(type);
          } finally {
            process.exit(0);
          }
        });
      });
    } else {
      this.logger.debug('No process found. Gracefull shutdown not possible');
    }
  }

  public exitProcess = async (type: string): Promise<void> => {
    console.info(
      `Got ${type}. Graceful shutdown Kafka start`,
      new Date().toISOString(),
    );
    this.logger.debug('Disconnecting consumer and producer');

    try {
      await this.consumer?.disconnect();
      this.logger.debug('Consumer disconnected');
    } catch (error) {
      this.logger.error('Error while disconnecting consumer', error);
    }

    try {
      await this.producer?.disconnect();
      this.logger.debug('Producer disconnected');
    } catch (error) {
      this.logger.error('Error while disconnecting producer', error);
    }

    console.info('Graceful shutdown Kafka complete', new Date().toISOString());
    process.exit(0);
  };

  private getAuthProviderOptions(config: AwsKafkaSaslConfig | undefined) {
    if (!config) {
      return {};
    }

    const options = {
      region: config.region ?? process.env['AWS_REGION'] ?? 'eu-west-1',
      credentials: undefined as any,
    };

    if (config.accessKeyId && config.secretAccessKey) {
      options.credentials = {
        accessKeyId: config.accessKeyId,
        secretAccessKey: config.secretAccessKey,
      };
    }

    return {
      ssl: true,
      sasl: {
        mechanism: Type,
        authenticationProvider: awsIamAuthenticator(options),
      },
    };
  }

  private getCorrespondingMonitor(): MessageMonitorInterface | undefined {
    if (this.baseYamlConfig.kafka.messageMonitor?.type === 'in-memory') {
      if (this.baseYamlConfig.debug)
        this.logger.debug('Using in-memory message monitor');
      return new InMemoryMessageMonitor(
        this.baseYamlConfig.kafka.messageMonitor,
        this.baseYamlConfig,
      );
    }

    if (this.baseYamlConfig.kafka.messageMonitor?.type === 'redis') {
      if (this.baseYamlConfig.debug)
        this.logger.debug('Using Redis message monitor');
      return new RedisMessageMonitor(
        this.baseYamlConfig.kafka.messageMonitor,
        this.baseYamlConfig,
      );
    }

    if (this.baseYamlConfig.kafka.messageMonitor?.type === 'disabled') {
      if (this.baseYamlConfig.debug)
        this.logger.debug('Message monitor disabled');
      return undefined;
    }

    if (this.baseYamlConfig.debug)
      this.logger.debug('No setting, Message monitor disabled');
    return undefined;
  }

  private checkForNewTopics = async (): Promise<boolean> => {
    const applicableTopics = await this.getRegexTopics();
    const numberOfApplicableTopics = applicableTopics.length;
    return numberOfApplicableTopics !== this.numberOfSubscribedRegexTopics;
  };

  protected getRegularTopics = (): Array<string> => {
    return (this.baseYamlConfig.kafka.consumerTopics ?? []).filter(
      (t) => !isTopicRegex(t),
    ) as Array<string>;
  };

  protected getRegexTopics = async (): Promise<Array<string>> => {
    const regexTopics = (this.baseYamlConfig.kafka.consumerTopics ?? []).filter(
      (t) => isTopicRegex(t),
    );

    if (regexTopics.length === 0) {
      return [];
    }

    const regexList = regexTopics.map(
      (topic) => new RegExp(topic.pattern, topic.flags),
    );

    const admin = this.kafkaBase.admin();
    await admin.connect();
    const existingTopics = await admin.listTopics();
    await admin.disconnect();

    return existingTopics.filter((topic) =>
      regexList.some((regex) => regex.test(topic)),
    );
  };

  private restartIfNewTopics = async (newTopics: boolean): Promise<void> => {
    if (newTopics) {
      try {
        this.logger.debug('New topics found, restarting consumer');
        await this.consumer.disconnect();
        await this.init();
      } catch (error) {
        captureException(error);
        process.exit(1);
      }
    }
  };
}
